<template>
  <div id="app">
    <dazzle-text :style="dazzleTextStyle" />
    <heart></heart>
    <dazzle-text :style="bottomStyle" />
    <audio autoplay loop :src="require('@/assets/music.mp3')"></audio>
    <div class="cake">
      <img :src="require('@/assets/cake.png')" alt="">
      <img :src="require('@/assets/cake.png')" alt="">
      <img :src="require('@/assets/cake.png')" alt="">
      <img :src="require('@/assets/cake.png')" alt="">
    </div>
  </div>
</template>

<script>
import DazzleText from "./components/dazzle-text.vue";
import Heart from "./components/heart.vue";

export default {
  name: "app",
  components: { DazzleText, Heart },
  data() {
    return {
      dazzleTextStyle: {},
      bottomStyle: {}
    };
  },
  mounted() {
    this.dazzleTextStyle = {
      marginTop: "100px"
    };
    this.bottomStyle = {
      marginTop: "500px",
      position: "absolute",
      bottom: "100px"
    };
  }
};
</script>

<style>
body {
  background: linear-gradient(to right, #141e30, #243b55);
}
.cake{
  position: relative;
  -webkit-animation-name: fadeIn1; /*动画名称*/
  -webkit-animation-duration: 5s; /*动画持续时间*/
  -webkit-animation-iteration-count: 1; /*动画次数*/
  -webkit-animation-delay: 0s; /*延迟时间*/
}
.cake img{
  position: absolute;
  width: 350px;
}
.cake img:nth-of-type(1){
  left: 0;
  top: 0;
}
.cake img:nth-of-type(2){
  right: 0;
  top: 0;
}
.cake img:nth-of-type(3){
  top: 800px;
  left: 0;

}
.cake img:nth-of-type(4){
  top: 800px;
  right: 0;
}
@-webkit-keyframes fadeIn1 {
  0% {
    opacity: 0; /*初始状态 透明度为0*/
  }
  25% {
    opacity: 0; /*初始状态 透明度为0*/
  }
  100% {
    opacity: 1; /*中间状态 透明度为0*/
  }
}
</style>

